.login-hero {
  background: url("../../media/login-hero.jpg") center center no-repeat;
  background-size: cover;
  color: white;
  display: none !important;
  padding: 0 30px !important;
}
@media (min-width: 768px) {
  .login-hero {
    display: flex !important;
  }
}
@media (min-width: 1600px) {
  .login-hero {
    padding: 0 100px !important;
  }
}
.login-hero__title {
  font-size: 48px;
}
.login-hero__text {
  font-size: 18px;
}
.login-hero img {
  max-height: 400px;
  display: block;
  margin: 0 auto;
  max-width: 80%;
  margin-top: 30px;
}
@media (min-width: 1600px) {
  .login-hero img {
    max-width: none;
    max-height: none;
  }
}

.auth-body {
  margin: 10px;
  height: calc(100vh - 20px);
}

.auth-container {
  background: #f0f6f3;
  border-radius: 10px;
  overflow: hidden;
}

.auth-logo {
  display: block;
  width: 255px;
}
.auth-logo img {
  max-width: 100%;
}

.form-with-logo {
  max-width: 80%;
}